* {
  color: inherit;
  z-index: 5;
  position: relative;
  letter-spacing: 0.04em;
  font-weight: 300;
}
*:focus {
  outline: none;
}
html {
  max-width: 100vw;
  overflow: hidden;
}
@media screen and (max-width: $iphone6H) {
  html {
    font-size: 52px;
  }
}
@media screen and (max-width: $iphone) {
  html {
    font-size: 46px;
  }
}
html body #app {
  display: flex;
  flex: 1 0 auto;
  flex-flow: column nowrap;
  height: 100%;
}
html body #app section {
  width: 100%;
  min-width: 100vw;
  flex: 1 0 auto;
  max-width: 100%;
  width: 100vw;
  min-width: 100%;
  overflow: hidden;
}
html body #app section .container {
  flex: 1 0 auto;
  margin: auto;
  display: flex;
  align-items: flex-start;
  max-width: $max-width;
  width: 100%;
  padding: 0 $overpadding;
}
html body #app button {
  cursor: pointer;
  width: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (max-width: $iphoneH) {
  html body #app button {
    font-size: 100%;
  }
}
html body #app button .message {
  color: $specialRed;
  font-size: 27%;
}
html body #app button.blank .message {
  text-align: center;
}
html body #app button.left.red {
  justify-content: flex-end;
}
html body #app button.left.red .button_inner {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10rem;
  min-height: 0.9rem;
  line-height: 0.9rem;
  max-width: 3.7rem;
  width: 100%;
  transition: $transition;
  background-color: $specialRed;
}
html body #app button.left.red .button_inner:hover {
  background-color: $darkhover;
  max-width: 4rem;
}
html body #app button.left.red .button_inner .message {
  position: absolute;
  left: 0;
  top: 0;
  transition: $transition;
  white-space: nowrap;
  color: $white;
  font-weight: 400;
  font-size: 27%;
  width: 100%;
  height: 100%;
  padding: 0;
  text-align: center;
}
html body #app button.left.red .button_inner .arrows {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: $transition;
  position: absolute;
  left: 0;
  top: 0;
}
html body #app button.left.red .button_inner .arrows:after,
html body #app button.left.red .button_inner .arrows:before {
  content: '';
  display: block;
  width: 0.2rem;
  height: 0.2rem;
  margin: auto;
  background-repeat: no-repeat;
  transition: $transition;
  opacity: 0;
  position: absolute;
  top: calc('50% - 0.1rem');
}
html body #app button.left.red .button_inner .arrows:before {
  left: 0.4rem;
  background-position: 0%;
  margin: 0 auto 0 0;
}
html body #app button.left.red .button_inner .arrows:after {
  right: 0.4rem;
  background-position: 100%;
  margin: 0 0 0 auto;
  background-image: url("../images/icon_arrow_white_right.svg");
}
html body #app button.left.red .button_inner .arrows:before {
  opacity: 1;
}
html body #app button.left.red .button_inner .arrows:after {
  opacity: 0;
}
html body #app button.left.red .button_inner:hover .message {
  left: 0.15rem;
}
